.nav-wrapper {
  position: absolute;
  bottom: 10vh;
  right: 10vh;
}
.Header {
  position: relative;
}
.sticky-nav-wrapper {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  top: 40px;
  z-index: 5;
}

.sticky-nav-wrapper > * {
  width: 300px;
}
.Us {
  background-image: url(/elevate-innovate-dev/static/media/stock-teamwork.59f028e6.png);
  background-repeat: no-repeat;
  background-size: 100%;
  font-family: 'Rokkitt', serif;
  height: 100vh;
  margin: 400px 0;
  min-height: 900px;
}

h2 {
  font-size: 44px;
}

p {
  font-size: 32px;
}

.align-left {
  display: flex;
  height: 100%;
  width: 100%;
}

.flex-center {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.parallax-content {
  position: relative;
}

.us {
  position: relative;
}

.us-sticky {
  position: -webkit-sticky;
  position: sticky;
  right: 20px;
  top: 20px;
  z-index: 2;
}

.us-wrapper {
  color: #fff;
  height: calc( 100vw * 2 / 3);
  margin: 0 100px;
  /* padding: 0 300px; */
  position: relative;
  width: 500px;
}

.us-wrapper > * {
  text-align: center;
}
.Skills {
  font-family: 'Rakkas', cursive;
  margin: 200px 0;
  position: relative;
  overflow: hidden;
}

.round-ds {
  border-radius: 4px;
  box-shadow: -2px 2px 4px rgba(0, 0, 0, 0.365);
}

.sketches {
  width: 353px;
  height: 280px;
}

.dusseDisp {
  width: 497px;
  height: 373px;
}

.dusseCab {
  width: 497px;
  height: 373px;
}

.hennesy {
  width: 339px;
  height: 339px;
}

.liquorDisp {
  width: 385px;
  height: 514px;
}

.capMorgan {
  width: 620px;
  height: 464px;
}

.rect {
  width: 100%;
  height: auto;
}

.rect-wrapper {
  align-items: center;
  display: flex;
  width: auto;
  height: calc(100% * 2776 / 1440); /* 50vw * (.rect-> height/width)  */
}

