} */
.Design {
  position: relative;
}

.center {
  align-items: center;
  display: flex;
  justify-content: center;
}

.design-grid {
  display: grid;
  grid-template-columns: 450px 1fr;
  padding: 0 5vw;
}

.design-img-small {
  width: 80%;
}

.design-img-big {
  width: 80%;
}

.floater-samples {
  height: 400px;
}

.grey-goose {
  grid-row: span 3;
}
.big-develop-img {
  width: 80%;
  margin-right: 8%;
}

.big-img-container {
  display: flex;
  justify-content: flex-end;
  grid-row: span 3;
}

.develop-body {
  margin: 0;
}

.develop-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.develop-h2 {
  margin: 0;
  margin-top: 40px;
}

.design-text-content > * {
  margin: 0;
}

.small-develop-img {
  margin-top: 15%;
  width: 80%;
}
.deliver-grid {
  display: grid;
  grid-template-columns: 40fr 60fr;
}

.big-deliver-img {
  width: 80%;
  -webkit-transform: translateX(-15%);
          transform: translateX(-15%);
}

.small-deliver-img {
  width: 80%;
}

.small-img-container {
  grid-row: span 2;
}
.Clients {
  width: 100%;
  margin: 400px 0;
}

.clients-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 200px);
  grid-gap: 60px;
  margin: auto 200px;
  width: 100%;
}

.clients-grid > * {
  width: 100%;
}

.clients-title {
  display: flex;
  justify-content: center;
}
.Contact {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  height: 100vh;
  min-height: 640px;
  justify-content: center;
  padding-left: 200px;
}

.contact-address {
  margin:  40px 0;
}

.contact-address > p {
  margin: 0;
}

.contact-links {
  margin: 40px 0;
}

.contact-links > a {
  font-size: 32px;
  margin-right: 10px;
  text-decoration: none;
}

.contact-links > a:visited {
  color: #0000ee;
}
.site-background {
  background-image: url(/elevate-innovate-dev/static/media/blueprint.6855a9b7.jpg);
  background-size: 100vw 100vh;
  height: 100vh;
  -webkit-filter: opacity(.1);
          filter: opacity(.1);
  position: fixed;
  top: 0;
  width: 100%;
  z-index: -99;
}
